import "./product-card.styles.scss";
import { useContext, useState } from "react";
import { CartContext } from "../../contexts/cart.context";
import Button from "../button/button.component";
import { useSelector } from "react-redux";
import { selectCurrentUser } from "../../store/user/user.selector";
import Toast from "../additional-components/toast/toast.component";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { useNavigate } from "react-router-dom";



const ProductCard = ({ product }) => {
  const { name, price, imageUrl , id  } = product;
  const [showAlert, setShowAlert] = useState(false);
  const [showToast, setShowToast] = useState(false);
  const currentUser = useSelector(selectCurrentUser);
  const { addItemToCart } = useContext(CartContext);
const Navigate = useNavigate()
  const addProductToCart = () => {
    if (currentUser) {
      addItemToCart(product);
      setShowToast(true);
      setTimeout(() => {
        setShowToast(false);
      }, 5000);
    } else {
      setShowAlert(true);
      setTimeout(() => {
        setShowAlert(false);
      }, 5000);
    }
  };

  return (

    <div className="product-card-container">
      <LazyLoadImage
        className="image"
        src={imageUrl[0]}
        alt={`${name}`}
        loading="lazy"
      />
      <div className="product-card-details">
        <span className="name">{name}</span>
        <span className="price"><span>{price}</span><span>تومان</span></span>
      </div>
      <div className="buttons">
      <Button buttonType="invertedAddToCart" onClick={addProductToCart}>
        افزودن به سبد خرید
      </Button>
      <Button onClick={()=> Navigate(`productview/${id}`)} buttonType="invertedAddToCart">
        مشاهده
      </Button>
      </div>
      {showAlert && (
        <Toast color="red" message="لطفا برای افزودن موارد به سبد خرید وارد حساب خود شوید!" />
      )}
      {showToast && (
        <Toast color="green" message="مورد با موفقیت به سبد خرید اضافه شد!" />
      )}

    </div>


  );
};

export default ProductCard;



